.lp-container {
  max-height: 100vw;
  max-width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #f8f9fa;

  section {
    width: 100%;
    height: 100vh;
    padding: 2rem 5rem;
  }

  .lp-hero {
    background-image: url('../../assets/images/background/bg-hero_2.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 7rem;

    .navbar {
      width: 100%;
      display: flex;
      justify-content: space-between;
      background-color: white;
      border-radius: 14px;
      padding: 20px 30px;

      .logo {
        width: 200px;
        height: 60px;
        background-image: url('../../assets/images/logos/pema-logo-lp.png');
        background-size: contain;
        background-repeat: no-repeat;
      }

      .menu {
        display: flex;
        gap: 25px;
        align-items: center;

        a,
        button {
          font-size: 14px;
        }

        a {
          color: #6a6e73;
          text-decoration: none;

          &:hover {
            text-decoration: underline;
          }
        }
      }
    }

    .title__search {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 30px;
      animation: search-fade 1500ms ease;
      //   margin-top: -80px;
    }

    // @keyframes search-fade {
    //   to {
    //     margin-top: -80px;
    //   }
    //   from {
    //     margin-top: 0px;
    //   }
    // }

    .title {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 5px;

      & h2 {
        color: white;
        font-size: 42px;
        font-weight: bold;
      }

      .title-line {
        width: 80px;
        height: 4px;
        background-color: white;
      }
    }

    .search {
      width: 90%;
      display: flex;
      gap: 20px;
      background-color: rgba(255, 255, 255, 0.338);
      justify-content: space-between;
      padding: 25px;
      border-radius: 14px;

      input,
      select,
      button {
        font-size: 14px;
      }

      input {
        height: 40px;
      }
    }
  }

  .lp-category {
    display: flex;
    justify-content: center;
    margin-top: -100px;

    .content {
      width: 90%;
      height: 210px;
      background-color: white;
      border-radius: 14px;
    }
  }

  .items {
    margin-top: -100px;
  }
}
